.breadcrumbs__items {color: rgba(34, 34, 34, .52); line-height: 1.4667; font-size: 13px;}
.breadcrumbs__items--top {color: rgba(255, 255, 255, .52);}
.breadcrumbs__item {display: inline; letter-spacing: 1.3px;}
.breadcrumbs__item + .breadcrumbs__item::before {content: ''; margin: 0 9px 0 5px; font-size: inherit; display: inline-block; vertical-align: middle; position: relative; top: -2px; height: 10px; width: 20px; background: url(../img/breadcrumb-arrow.png) no-repeat top left;}
.breadcrumbs__link {color: #222;}
.breadcrumbs__link--top {color: var(--color-white);}